@font-face {
  font-family: colege;
  src: url('./css/fontes/CollegiateheavyoutlineMedium-B0yx.ttf');
}

@font-face {
  font-family: quentin;
  src: url('./css/fontes/Quentincaps-owxKz.ttf');
}

@font-face {
  font-family: godin;
  src: url('./css/fontes/DigitaltsOrange-nRAPg.ttf');
}

@font-face {
  font-family: curseBook;
  src: url('./css/fontes/Playball-q6o1.ttf');
}


.fontTitle {
  font-family: 'colege';
  color: greenyellow;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media ( prefers-reduced-motion : no-preference ) {

  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  align-items: center;
  background-color: #282c34;
  color: white;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  justify-content: center;
  min-height: 100vh;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {

  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}
